<template>
  <Navbar />
  <div class="complaints-progress">
    <h4 class="title">Handled Complaints</h4>
    <progress min="0" max="100" value="40"></progress>
  </div>
  <div class="complaints-topbar">
    <div class="complaints-urgency">
      <h4 class="title">Urgency</h4>
      <div class="options">
        <p :class="{ urgency: urgency == 'high' }" @click="urgency = 'high'">
          High
        </p>
        <p
          :class="{ urgency: urgency == 'medium' }"
          @click="urgency = 'medium'"
        >
          Medium
        </p>
        <p :class="{ urgency: urgency == 'low' }" @click="urgency = 'low'">
          Low
        </p>
      </div>
    </div>
    <div class="search-filter-container">
      <div class="custom-search">
        <input type="text" placeholder="Search..." v-model="searchValue" />
      </div>
      <select name="sort" id="sort" v-model="selected">
        <option value="desc" selected>Latest</option>
        <option value="asc">Oldest</option>
      </select>
    </div>
  </div>
  <div class="complaints" v-if="!loading">
    <ComplaintCard
      v-for="(data, index) in complaints"
      :key="index"
      :title="data.title"
      :description="data.description"
      :username="data.username"
      :date="new Date(data.date)"
      @click="() => router.push({ path: '/complaints/1' })"
    />
  </div>
</template>
<script setup>
import Navbar from "@/components/NavBar.vue";
import ComplaintCard from "@/components/ComplaintCard.vue";

import { ref, watch, onMounted } from "vue";
import { useRouter } from "vue-router";

const selected = ref("");
const urgency = ref("high");
const router = useRouter();
const loading = ref(false);
const searchValue = ref("");

const complaints = ref([]);
const moreComplaints = [
  {
    username: "John Smith",
    date: "2023-03-11T13:15:00Z",
    urgency: "high",
    title: "Damaged Item",
    description:
      "The item I received is damaged and cannot be used. Please send a replacement or provide a refund.",
  },
  {
    username: "Jane Doe",
    date: "2023-03-10T09:30:00Z",
    urgency: "medium",
    title: "Wrong Item",
    description:
      "I received the wrong item in my order. I ordered a jacket, but I received a T-shirt. Please send the correct item or provide a refund.",
  },
  {
    username: "Max Wilson",
    date: "2023-03-09T16:45:00Z",
    urgency: "low",
    title: "Late Delivery",
    description:
      "My order was supposed to be delivered yesterday, but it has not arrived yet. Please let me know when I can expect it to be delivered.",
  },
  {
    username: "Sara Davis",
    date: "2023-03-08T11:00:00Z",
    urgency: "high",
    title: "Defective Product",
    description:
      "The product I received is defective and does not work properly. I need a replacement or a refund.",
  },
  {
    username: "John Johnson",
    date: "2023-03-07T14:30:00Z",
    urgency: "medium",
    title: "Missing Parts",
    description:
      "The product I received is missing parts and cannot be assembled. Please send the missing parts or provide a refund.",
  },
  {
    username: "Kate Lee",
    date: "2023-03-06T08:45:00Z",
    urgency: "low",
    title: "Wrong Size",
    description:
      "The item I received is the wrong size. I ordered a medium, but received a small. Please send the correct size or provide a refund.",
  },
  {
    username: "Peter Brown",
    date: "2023-03-05T12:15:00Z",
    urgency: "high",
    title: "Wrong Color",
    description:
      "The item I received is the wrong color. I ordered a black item, but received a white one. Please send the correct color or provide a refund.",
  },
  {
    username: "Olivia Green",
    date: "2023-03-04T10:00:00Z",
    urgency: "medium",
    title: "Missing Item",
    description:
      "I received my order, but one of the items is missing. Please send the missing item or provide a refund for it.",
  },
  {
    username: "Alex Perez",
    date: "2023-03-03T15:30:00Z",
    urgency: "low",
    title: "Incorrect Price",
    description:
      "The price of the item I received is incorrect. It was advertised at $50, but I was charged $75. Please refund the difference.",
  },
  {
    username: "Emily Anderson",
    urgency: "medium",
    title: "Incorrect Price",
    date: "2023",
    description:
      "The price of the item I received is incorrect. It was advertised at $50, but I was charged $75. Please refund the difference.",
  },
];

onMounted(() => {
  complaints.value = getFilteredComplaints(urgency.value);
});

watch(searchValue, () => {
  if (!searchValue.value) return;
  complaints.value = moreComplaints.filter(
    (val) =>
      (val.description
        .toLowerCase()
        .includes(searchValue.value.toLowerCase()) ||
        val.title.toLowerCase().includes(searchValue.value.toLowerCase()) ||
        val.username.toLowerCase().includes(searchValue.value.toLowerCase())) &&
      val.urgency == urgency.value
  );
});

watch(urgency, () => {
  loading.value = true;
  searchValue.value = "";
  complaints.value = getFilteredComplaints(urgency.value);
  if (selected.value) sortComplaints(selected.value);
  loading.value = false;
});

watch(selected, () => {
  sortComplaints(selected.value);
});

const getFilteredComplaints = (urgency) => {
  return moreComplaints.filter((complaint) => complaint.urgency == urgency);
};

const sortComplaints = (val) => {
  loading.value = true;
  switch (val) {
    case "asc":
      complaints.value = getFilteredComplaints(urgency.value).sort((a, b) => {
        return new Date(a?.date).getTime() - new Date(b?.date).getTime();
      });
      break;
    case "desc":
      complaints.value = getFilteredComplaints(urgency.value).sort(
        (b, a) => new Date(a?.date).getTime() - new Date(b?.date).getTime()
      );
      break;
    default:
      break;
  }
  loading.value = false;
};
</script>
<style scoped>
.complaints-progress,
.complaints-urgency {
  text-align: left;
  margin-left: 30px;
  display: flex;
  flex-direction: column;
}

.title {
  margin-top: 40px;
}

.complaints-topbar {
  display: flex;
  justify-content: space-between;
}

.search-filter-container {
  display: flex;
  align-items: center;
  gap: 50px;
}

select,
input[type="text"] {
  padding: 5px 10px;
}

.complaints-urgency {
  margin-top: 20px;
}

.urgency {
  border-bottom: 2px solid blue;
}

.options {
  display: flex;
  width: 200px;
  margin-top: -20px;
  justify-content: space-between;
}

.options > p {
  cursor: pointer;
  padding-bottom: 2px;
}

progress {
  margin-top: -15px;
  width: 300px;
  height: 20px;
}

.complaints {
  margin-top: 20px;
  padding-left: 30px;
  display: grid;
  gap: 15px 5px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 350px));
}
</style>
